<template lang="html">
  <div class="group" >
    <label for="companys">Kompanija: </label>
    <select name="type" id="type" v-model="type" @change="onChangeCompany($event)">
       <option  v-for="company in companys" v-bind:key="company.name">{{company.name}}</option>
    </select>
  </div>


  <div class="kompetence">
    <h2>Kompetence</h2>
    <div class="kpi" v-for="(komp,i) in kompetence" v-bind:key="i">
      <input
        class="form-control"
        type="text"
        v-model="komp.name"
        >
      <input
          class="form-control"
          type="number"
          v-model="komp.ponder"
          >
      <button type="button" name="button" @click="ukloniKompetencu(i)">ukloni</button>
    </div>
    <button type="button" name="button" @click="increaseKompetence">Dodaj kompetencu</button>

  </div>

  <div class="KompanijskiKpi">
    <h2>Kompanijski Kpi</h2>

    <div class="kpi" v-for="(komp,i) in kompanijskiKpi" v-bind:key="i">
      <input
        class="form-control"
        type="text"
        v-model="komp.name"
        >
      <input
          class="form-control"
          type="number"
          v-model="komp.ponder"
          >
      <button type="button" name="button" @click="ukloniKompanijski(i)">ukloni</button>
    </div>
    <button type="button" name="button" @click="increaseKompanijskiKpi">Dodaj Kpi</button>

  </div>

  <button type="button" name="button" @click="submit">Submit</button>

</template>

<script>
  // eslint-disable-next-line no-unused-vars
import {mapActions,mapGetters} from 'vuex';

export default {
    data(){
      return{
        kompetence:[],
        kompanijskiKpi:[],
        currentCompany:"",
      }
    },
    computed: mapGetters(['companys']),
    methods:{
        ...mapActions(['getCompanies','setCompanyKpi']),
        submit(){
            let kpiData = {
              kompetence:this.kompetence,
              kompanijski:this.kompanijskiKpi,
              companyName:this.currentCompany,
            }
            this.setCompanyKpi(kpiData);
        },
        increaseKompetence(){
          let komp = {
            name:"",
            ponder:0
          }
          this.kompetence.push(komp);

        },
        increaseKompanijskiKpi(){
          let komp = {
            name:"",
            ponder:0
          }
          this.kompanijskiKpi.push(komp);

        },
        ukloniKompetencu(i){
          this.kompetence.splice(i,1);
        },
        ukloniKompanijski(i){
          this.kompanijskiKpi.splice(i,1);
        },
        onChangeCompany(event) {
            this.kompetence=[];
            this.kompanijskiKpi=[];
            let tempCompany;
            for (var i = 0; i < this.companys.length; i++) {
              if(this.companys[i].name==event.target.value){
                tempCompany=this.companys[i];
              }
            }
            this.currentCompany= tempCompany.name;
            //Kompetence
            for ( i = 0; i < tempCompany.performanceEvaluation.kompetence.length; i++) {
              let komp = {
                name:tempCompany.performanceEvaluation.kompetence[i].name,
                ponder:tempCompany.performanceEvaluation.kompetence[i].ponder
              }
              this.kompetence.push(komp);
            }

            //KompanijskiKpi
            for ( i = 0; i < tempCompany.performanceEvaluation.kompanijski.length; i++) {
              let komp = {
                name:tempCompany.performanceEvaluation.kompanijski[i].name,
                ponder:tempCompany.performanceEvaluation.kompanijski[i].ponder
              }
              this.kompanijskiKpi.push(komp);
            }

        },
    },
    created(){
      this.getCompanies();

    }
}
</script>


<style lang="css" scoped>
.kpi{
  display: grid;
 grid-template-columns: 1fr 1fr 1fr;
 grid-template-rows: 1fr;
 gap: 0px 0px;
 grid-template-areas:
   ". . .";
}
</style>
